conjugator.not_s_irregular = {'': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true};

conjugator.not_d_irregular = {'': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, ' ': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true};

conjugator.not_h_irregular = {'': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true, '': true};

conjugator.not_l_euh_irregular = {'': true, '': true, '': true, '': true, '': true, '': true, '': true};

conjugator.not_l_irregular = {};

conjugator.after_last_space = function(infinitive) {
    return infinitive.split(' ').reverse()[0];
};

conjugator.is_s_irregular = function(infinitive, regular) {
    if (regular) {
        return false;
    }
    return hangeul.match(infinitive.charAt(infinitive.length-1), '*', '*', '') &&
           !(conjugator.after_last_space(infinitive) in conjugator.not_s_irregular);
};


conjugator.is_l_irregular = function(infinitive, regular) {
    if (regular) {
        return false;
    }
    return hangeul.match(infinitive.charAt(infinitive.length-1), '*', '*', '') &&
           !(conjugator.after_last_space(infinitive) in conjugator.not_l_irregular);
}

conjugator.is_l_euh_irregular = function(infinitive, regular) {
    if (regular) {
        return false;
    }
    return hangeul.match(infinitive.charAt(infinitive.length-1), '', '', null) &&
           !(conjugator.after_last_space(infinitive) in conjugator.not_l_euh_irregular);
};

conjugator.is_h_irregular = function(infinitive, regular) {
    if (regular) {
        return false;
    }
    return (hangeul.padchim(infinitive.charAt(infinitive.length-1)) == '' ||
           infinitive.charAt(infinitive.length-1) == '') &&
           !(conjugator.after_last_space(infinitive) in conjugator.not_h_irregular);
};

conjugator.is_p_irregular = function(infinitive, regular) {
    if (regular) {
        return false;
    }
    return hangeul.match(infinitive.charAt(infinitive.length-1), '*', '*', '') &&
           !(conjugator.after_last_space(infinitive) in conjugator.not_p_irregular);
};

conjugator.is_d_irregular = function(infinitive, regular) {
    if (regular) {
        return false;
    }
    return hangeul.match(infinitive.charAt(infinitive.length-1), '*', '*', '') &&
           !(conjugator.after_last_space(infinitive) in conjugator.not_d_irregular);
};

conjugator.verb_types = {
    '   (irregular verb)': conjugator.is_s_irregular,
    '   (irregular verb)': conjugator.is_l_irregular,
    '   (irregular verb)': conjugator.is_l_euh_irregular,
    '   (irregular verb)': conjugator.is_h_irregular,
    '   (irregular verb)': conjugator.is_p_irregular,
    '   (irregular verb)': conjugator.is_d_irregular
};

conjugator.verb_type = function(infinitive, regular) {
    if (regular) {
        return 'regular verb';
    }
    for (irregular_name in conjugator.verb_types) {
        func = conjugator.verb_types[irregular_name];
        if (func(conjugator.base(infinitive))) {
            return irregular_name;
        }
    }
    return 'regular verb';
}

conjugator.base = function(infinitive, regular) {
    if (infinitive.charAt(infinitive.length-1) == '') {
        return infinitive.substring(0, infinitive.length-1);
    } else {
        return infinitive;
    }
};

conjugator.base2 = function(infinitive, regular) {
    infinitive = conjugator.base(infinitive, regular);

    if (infinitive == '') {
        infinitive = new hangeul.Geulja('');
        infinitive.hidden_padchim = true;
        return infinitive;
    }
    if (infinitive == '') {
        return '';
    }
    if (infinitive == '') {
        return '';
    }
    let new_infinitive = infinitive;
    if (conjugator.is_h_irregular(infinitive, regular)) {
        new_infinitive = conjugator.merge(infinitive.substring(0, infinitive.length-1) +
                                          hangeul.join(hangeul.lead(infinitive.charAt(infinitive.length-1)),
                                                       hangeul.vowel(infinitive.charAt(infinitive.length-1))),
                                          '');
        conjugator.reasons.push(' irregular (' + infinitive + ' -> ' + new_infinitive + ')');
    } else if (conjugator.is_p_irregular(infinitive, regular)) {
        // only some verbs get  (highly irregular)
        let new_vowel;
        if (infinitive in {'': true} ||
            infinitive.charAt(infinitive.length-1) in {'': true, '': true}) {
            new_vowel = '';
        } else {
            new_vowel = '';
        }
        new_infinitive = conjugator.merge(infinitive.substring(0, infinitive.length-1) +
                                          hangeul.join(hangeul.lead(infinitive.charAt(infinitive.length-1)),
                                                       hangeul.vowel(infinitive.charAt(infinitive.length-1))),
                                          hangeul.join('', new_vowel))
        conjugator.reasons.push(' irregular (' + infinitive + ' -> ' + new_infinitive + ')');
    } else if (conjugator.is_d_irregular(infinitive, regular)) {
        new_infinitive = new hangeul.Geulja(infinitive.substring(0, infinitive.length-1) +
                                            hangeul.join(hangeul.lead(infinitive.charAt(infinitive.length-1)),
                                            hangeul.vowel(infinitive.charAt(infinitive.length-1)),
                                             ''));
        new_infinitive.original_padchim = '';
        conjugator.reasons.push(' irregular (' + infinitive + ' -> ' + new_infinitive + ')');
    } else if (conjugator.is_s_irregular(infinitive, regular)) {
        new_infinitive = new hangeul.Geulja(infinitive.substring(0, infinitive.length-1) +
                                            hangeul.join(hangeul.lead(infinitive.charAt(infinitive.length-1)),
                                            hangeul.vowel(infinitive.charAt(infinitive.length-1))));
        new_infinitive.hidden_padchim = true;
        conjugator.reasons.push(' irregular (' + infinitive + ' -> ' + new_infinitive + ' [hidden padchim])');
    }
    return new_infinitive;
};

conjugator.base3 = function(infinitive, regular) {
    infinitive = conjugator.base(infinitive, regular);
    if (infinitive == '') {
        return '';
    }
    if (infinitive == '') {
        return '';
    }
    if (infinitive == '') {
        return '';
    }
    if (conjugator.is_h_irregular(infinitive, regular)) {
        return infinitive.substring(0, infinitive.length-1) +
               hangeul.join(hangeul.lead(infinitive.charAt(infinitive.length-1)),
                            hangeul.vowel(infinitive.charAt(infinitive.length-1)))
    } else if (conjugator.is_p_irregular(infinitive, regular)) {
        return infinitive.substring(0, infinitive.length-1) +
               hangeul.join(hangeul.lead(infinitive.charAt(infinitive.length-1)),
                            hangeul.vowel(infinitive.charAt(infinitive.length-1))) + '';
    } else {
        return conjugator.base2(infinitive, regular);
    }
};

conjugator.declarative_present_informal_low = function(infinitive, regular, further_use) {
    infinitive = conjugator.base2(infinitive, regular);
    if (!further_use && ((infinitive == '' && !infinitive.hidden_padchim) ||
                         infinitive == '') ||
                        (regular && infinitive.charAt(infinitive.length-1) == '')) {
        conjugator.reasons.push(' irregular');
        return infinitive + '';
    }
    //  irregular
    if (regular && infinitive == '') {
        return '';
    }
    if (conjugator.is_l_euh_irregular(infinitive, regular)) {
        let new_base = infinitive.substring(0, infinitive.length-2) +
                   hangeul.join(hangeul.lead(infinitive[infinitive.length-2]),
                                hangeul.vowel(infinitive[infinitive.length-2]),
                                '');
        if (infinitive.substring(infinitive.length-2, infinitive.length) in {'': true, '': true}) {
            new_base = new_base + hangeul.join('',
                                               hangeul.vowel(hangeul.find_vowel_to_append(new_base)))
            conjugator.reasons.push('irregular stem + ' + infinitive + ' -> ' + new_base);
            return infinitive + '';
        } else if (hangeul.find_vowel_to_append(infinitive.substring(0, infinitive.length-1)) == '') {
            new_base += ''
            conjugator.reasons.push(' irregular stem change [' + infinitive + ' -> ' + new_base + ']');
            return new_base;
        } else {
            new_base += '';
            conjugator.reasons.push(' irregular stem change [' + infinitive + ' -> ' + new_base + ']');
            return new_base;
        }
    } else if (infinitive.charAt(infinitive.length-1) == '') {
        return conjugator.merge(infinitive, '');
    } else if (conjugator.is_h_irregular(infinitive, regular)) {
        return conjugator.merge(infinitive, '');
    }
    return conjugator.merge(infinitive, hangeul.find_vowel_to_append(infinitive));
};
conjugator.declarative_present_informal_low.conjugation = true;

conjugator.declarative_present_informal_high = function(infinitive, regular) {
    infinitive = conjugator.base2(infinitive, regular)
    if ((infinitive == '' && !infinitive.hidden_padchim) ||
        (infinitive.charAt(infinitive.length-1) == '' && regular) ||
        infinitive == '') {
        conjugator.reasons.push(' irregular')
        return infinitive + '';
    }
    return conjugator.merge(conjugator.declarative_present_informal_low(infinitive, regular, true), '');
};
conjugator.declarative_present_informal_high.conjugation = true;

conjugator.declarative_present_formal_low = function(infinitive, regular) {
    if (conjugator.is_l_irregular(conjugator.base(infinitive), regular)) {
        return conjugator.drop_l_and_borrow_padchim(conjugator.base(infinitive, regular), '');
    }
    return conjugator.merge(conjugator.base(infinitive, regular), '');
};
conjugator.declarative_present_formal_low.conjugation = true;

conjugator.declarative_present_formal_high = function(infinitive, regular) {
    if (conjugator.is_l_irregular(conjugator.base(infinitive), regular)) {
        return conjugator.drop_l_and_borrow_padchim(conjugator.base(infinitive, regular), '');
    }
    return conjugator.merge(conjugator.base(infinitive, regular), '')
};
conjugator.declarative_present_formal_high.conjugation = true;

conjugator.past_base = function(infinitive, regular) {
    let ps = conjugator.declarative_present_informal_low(infinitive, regular, true);
    if (hangeul.find_vowel_to_append(ps) == '') {
        return conjugator.merge(ps, '');
    } else {
        return conjugator.merge(ps, '');
    }
};
conjugator.past_base.conjugation = true;

conjugator.declarative_past_informal_low = function(infinitive, regular) {
    return conjugator.merge(conjugator.past_base(infinitive, regular), '');
};
conjugator.declarative_past_informal_low.conjugation = true;

conjugator.declarative_past_informal_high = function(infinitive, regular) {
    return conjugator.merge(conjugator.declarative_past_informal_low(infinitive, regular), '');
};
conjugator.declarative_past_informal_high.conjugation = true;

conjugator.declarative_past_formal_low = function(infinitive, regular) {
    return conjugator.merge(conjugator.past_base(infinitive, regular), '');
};
conjugator.declarative_past_formal_low.conjugation = true;

conjugator.declarative_past_formal_high = function(infinitive, regular) {
    return conjugator.merge(conjugator.past_base(infinitive, regular), '');
};
conjugator.declarative_past_formal_high.conjugation = true;

conjugator.future_base = function(infinitive, regular) {
    if (conjugator.is_l_irregular(conjugator.base(infinitive, regular))) {
        return conjugator.drop_l_and_borrow_padchim(conjugator.base3(infinitive, regular), '');
    }
    return conjugator.merge(conjugator.base3(infinitive, regular), '');
};
conjugator.future_base.conjugation = true;

conjugator.declarative_future_informal_low = function(infinitive, regular) {
    return conjugator.merge(conjugator.future_base(infinitive, regular), ' ');
};
conjugator.declarative_future_informal_low.conjugation = true;

